
import {
	getCoords,
	getWeather,
	checkResponse,
	filterData,
	updateDOM,
	catchError,
	getLocation
} from 'helpers/helpers';

getLocation()
	.then(getCoords)
	.then(getWeather)
	.then(checkResponse)
	.then(filterData)
	.then(updateDOM)
	.catch(catchError);



